/*SCSS code for DevTab*/
/* I had to write a little bit of Sass in this file cuz gulp wouldnt compile a blank .scss doc -JB*/
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  background-image: url("../assets/img/sampl2.jpeg");
  font-family: Lato, helvetica; }
  body .overlay {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.25) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.25) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.25) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */ }

.linkNote {
  display: flex;
  justify-content: space-around;
  /*----------------------------------------------------*/
  /*Quick Links and Sticky Notes*/
  /*----------------------------------------------------*/ }
  .linkNote .quickLinks {
    display: flex;
    margin: 20px auto 0px 40px;
    cursor: pointer; }
    .linkNote .quickLinks p {
      margin: 0px 0px 0px 0px;
      padding: 10px 8px 8px 8px;
      font-size: 1.4em;
      color: white; }
    .linkNote .quickLinks i {
      margin: 6px 6px 6px 20px;
      color: #D9D9D9;
      font-size: 1.75em; }
  .linkNote .stickyNotes {
    display: flex;
    margin: 5px 30px 10px 10px;
    cursor: pointer; }
    .linkNote .stickyNotes p {
      margin: 8px 0px;
      padding: 10px 5px 0px 0px;
      color: white;
      font-size: 2em; }
    .linkNote .stickyNotes i {
      margin: 10px 0px 0px 10px;
      padding: 10px 0px 0px 0px;
      color: white;
      font-size: 2.3em; }

.pomodoroTime {
  flex: 1 0 auto;
  margin: 150px 0px;
  padding: 0px;
  /*----------------------------------------*/
  /*Time and Pomodoro Display*/
  /*----------------------------------------*/ }
  .pomodoroTime .userGreeting {
    display: flex;
    justify-content: space-around;
    padding: 0px;
    margin: 0px 0px 20px 0px;
    color: white;
    font-size: 2em; }
    .pomodoroTime .userGreeting h1 {
      padding: 0px;
      margin: 0px; }
  .pomodoroTime .timeDisplay {
    display: flex;
    justify-content: space-around;
    padding: 0px;
    margin: 0px;
    font-weight: 700;
    color: white;
    font-size: 5.5em; }
    .pomodoroTime .timeDisplay p {
      padding: 0px;
      margin: 0px 20px 0px 0px; }

footer {
  width: 100%;
  height: 100px;
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  margin: 0px;
  padding: 0px; }
  footer .colorPallete {
    display: flex;
    width: 277px;
    padding: 0px;
    margin: 50px 0px 0px 30px;
    color: #D9D9D9;
    font-size: 2.3em; }
  footer p {
    margin: 60px 0px 0px 0px;
    padding: 0px;
    color: #D9D9D9;
    font-size: .9em; }
    footer p a {
      text-decoration: none;
      color: #D9D9D9; }
    footer p a:hover {
      border-bottom: 1px solid #D9D9D9; }
  footer .listArea {
    display: flex;
    width: 277px;
    margin: 40px 0px 0px 30px; }
    footer .listArea ul {
      display: flex;
      margin: 0px 10px 0px 0px;
      padding: 0px;
      list-style-type: none; }
      footer .listArea ul li {
        cursor: pointer;
        margin: 0px 15px 0px 25px;
        padding: 5px; }
        footer .listArea ul li i {
          margin: 0px;
          padding: 0px;
          color: #D9D9D9;
          font-size: 1.8em; }
        footer .listArea ul li .iconHover {
          opacity: 0;
          margin: 0px 0px 0px 0px;
          padding: 0px;
          color: white;
          font-size: 1em;
          transition: .5s; }
      footer .listArea ul li:hover .iconHover {
        opacity: 1;
        visibility: visible;
        transition: .5s; }
